.add_task h3 {
  margin: 5px 10px;
  font-size: 30px;
  text-shadow: 1px 1px 3px white;
}
.add_task__form p {
  margin: 5px 0;
}
.add_task__form textarea {
  padding: 5px; 
  margin: 5px;
  min-width: 280px;
  width: 90%;
  height: 90px; 
  outline: none;
  border: 2px solid #2d3b4d;
  font-size: 16px;
}
.invalid_val {
  border-color: coral;
}
.add_task__form button {
  line-height: 25px;
  width: 82px;
  border: 1px solid white;
  border-radius: 3px;
  background-color: transparent;
  color:#2d3b4d;
  text-shadow: 1px 1px 3px white;
  cursor: pointer;
  outline: none;
  margin: 0 15px;
}
.add_task__form button:hover {
  background-color: #2d3b4d;
  color: white;
  text-shadow: 1px 1px 3px #2d3b4d;
}


